import React,{useEffect} from 'react';
import {useDispatch , useSelector} from 'react-redux';
import * as eventAction from '../../redux/events/events.action';
import * as eventReducer from '../../redux/events/events.reducer';
import Spinner from '../../root/util/spinner/Spinner';


let ProEvents = ()=>{
    let dispatch = useDispatch();
    let eventInfo = useSelector((state)=>{
        return state[eventReducer.eventFeaturesKey]
    });

    let {loading ,events}= eventInfo;
    useEffect(()=>{
        dispatch(eventAction.getProEvents());
    },[]);
    return(
        <React.Fragment>
            {/*<pre>{JSON.stringify(events)}</pre>*/}
            <section className="p-3">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className="h2 text-secondary"><i className="fas fa-calendar-plus"/> Pro Events</p>
                            <p className="lead">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Autem laboriosam necessitatibus omnis quae repellat reprehenderit sequi ut. Asperiores consequatur est facere nam. Assumenda eligendi illo incidunt laudantium minus modi, neque possimus recusandae similique tempora veniam voluptates voluptatibus! Eaque, iure, maxime!</p>
                            <p className="h5 ">Total Available : {events.length}</p>
                        </div>
                    </div>
                </div>
            </section>
            {
                loading ? <Spinner/> :
                    <React.Fragment>
                        {
                            events.length > 0 ?
                                <React.Fragment>
                                    <section>
                                        <div className="container">
                                            <div className="row">
                                                <div className="col">
                                                    {
                                                        events.map(event => {
                                                            return (
                                                                <div className="card mt-3" key={event._id}>
                                                                    <img src={event.image} alt=""/>
                                                                    <div className="card-body bg-light">
                                                                        <div className="row">
                                                                            <div className="col">
                                                                                <p className="h4">{event.name}</p>
                                                                                <p>DATE : {event.date}</p>
                                                                                <h6>Price : {event.price}</h6>
                                                                            </div>
                                                                            <div className="col">
                                                                                <button className="btn btn-teal btn-sm">Book Now</button>
                                                                            </div>
                                                                        </div>
                                                                        <h4>About this Event</h4>
                                                                        <p>{event.info}</p>
                                                                    </div>
                                                                </div>
                                                            )
                                                        })
                                                    }
                                                </div>
                                            </div>
                                        </div>
                                    </section>
                                </React.Fragment> : null
                        }
                    </React.Fragment>
            }
            <div style={{marginBottom : '50px'}}/>
        </React.Fragment>
    )
};
export default ProEvents;