import * as eventsAction from '../events/events.action';
//features kay
export const eventFeaturesKey = 'events';

//initialState

let initialState = {
    loading : false,
    events :[],
    errorMessage:''
};


//reducer function



export  const reducer =(state = initialState , action)=>{
    let {type , payload}=action;
    switch (type){
        case eventsAction.UPLOADE_EVENT_REQUEST:
            return{
                ...state,
                loading :true
            };
        case eventsAction.UPLOADE_EVENT_SUCCESS:
            return{
                ...state,
                loading:false,
            };
        case eventsAction.UPLOADE_EVENT_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload
            };

            //get free events
        case eventsAction.GET_FREE_EVENT_REQUEST:
            return{
                ...state,
                loading :true
            };
        case eventsAction.GET_FREE_EVENT_SUCCESS:
            return{
                ...state,
                loading:false,
                events:payload.event
            };
        case eventsAction.GET_FREE_EVENT_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };

        //get Pro events
        case eventsAction.GET_PRO_EVENT_REQUEST:
            return{
                ...state,
                loading :true
            };
        case eventsAction.GET_PRO_EVENT_SUCCESS:
            return{
                ...state,
                loading:false,
                events:payload.event
            };
        case eventsAction.GET_PRO_EVENT_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload.error
            };
        default:return state;

    }
};

