import React from 'react';
import {Link,useHistory} from 'react-router-dom';
import {useDispatch , useSelector} from 'react-redux';
import eventNow from '../../../assets/image/events-now.png';
import * as userUtil from '../../../util/userUtil';
import * as useAction from '../../../redux/user/user.action';
import * as userReducer from '../../../redux/user/user.reducer'
let Navbar = ()=>{

    let dispatch = useDispatch();
    let history = useHistory();

    let logoutUser =()=>{
        dispatch(useAction.logoutUser(history));
    };
    let userInfo = useSelector((state) => {
        return state[userReducer.userFeaturesKey];
    });

    let {isAuthenticated , user} = userInfo;

let beforeLogin = (
    <React.Fragment>
        <li className="nav-item">
            <Link to="/users/login" className ="nav-link text-dark font-weight-bold"><i className="fa fa-user-circle  mr-1" />Login</Link>
        </li>

        <li className="nav-item">
            <Link to="/users/register" className ="nav-link text-dark font-weight-bold"><i className="fa fa-sign-in-alt  mr-1"/>Register</Link>
        </li>
    </React.Fragment>
);


let afterLogin = (
    <React.Fragment>
        <li className="nav-item">
            <Link to="/" className="nav-link text-white font-weight-bold">
                <img src={user?.avatar} width="25" height="25" className="rounded-circle" alt=""/> {user?.name}</Link>
        </li>
        <li className="nav-item">
            <Link to="/"  onClick={logoutUser} className ="nav-link text-dark font-weight-bold"><i className="fa fa-sign-in-alt  mr-1"/>logout</Link>
        </li>
    </React.Fragment>
);


    return(
        <React.Fragment>
            <nav className="navbar navbar-expand-sm navbar-dark bg-secondary ">
                <div className="container">
                        <Link to="/" className="nav-link">
                            <img src={eventNow} alt=""/>
                        </Link>
                    <div className="collapse navbar-collapse">
                        <ul className="navbar-nav ">
                            <li className="nav-item">
                                <Link to="/events/free" className ="nav-link text-dark font-weight-bold">Free Events</Link>
                            </li>
                            <li className="nav-item">
                                <Link to="/events/pro" className ="nav-link text-dark font-weight-bold">Pro Events</Link>
                            </li>
                            <li className="nav-item">
                                <Link to="/events/upload" className ="nav-link text-dark font-weight-bold">Upload</Link>
                            </li>

                        </ul>
                        <ul className="navbar-nav ml-auto">

                            {
                                userUtil.isLoggedIn()  ? afterLogin : beforeLogin
                            }

                        </ul>
                    </div>
                </div>
            </nav>
        </React.Fragment>
    )
};
export default Navbar;