import React,{useState} from 'react';
import * as eventsAction from '../../redux/events/events.action';
import {useDispatch , useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import * as userReducer from '../../redux/user/user.reducer';

let UploadEvents = ()=>{

    let dispatch = useDispatch();
    let history = useHistory();
    let[events , setEvents]= useState({
        name:'',
        image:'',
        date:'',
        type:'',
        price:'',
        info:''
    });

    let updateInput=(event)=>{
        setEvents({
            ...events,
            [event.target.name]:event.target.value
        });
    };
//submitEvents

    let submitEvents =(e)=>{
        e.preventDefault();
        dispatch(eventsAction.uploadEvent(events , history));
    };

let userInfo =useSelector((state)=>{
    return state[userReducer.userFeaturesKey]
});

let {user} = userInfo;

    return(
        <React.Fragment>
            {/*<pre>{JSON.stringify(user)}</pre>*/}
            {/*<pre>{JSON.stringify(events)}</pre>*/}
            <section className="p-3">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className="h4 text-secondary"><i className="fa fa-cloud-upload-alt"/>Upload an Event</p>
                            <p className="lead">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores aspernatur autem commodi consequatur cumque deserunt doloribus eligendi exercitationem minus mollitia neque nesciunt nihil officiis omnis, quae, qui quidem sit tempore?</p>
                            <p className="lead">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Doloremque esse id illum necessitatibus nulla, officiis provident rerum similique vero vitae.</p>
                        </div>
                    </div>
                </div>
            </section>
            {
                user.isAdmin ?
                    <React.Fragment>
                        <section>
                            <div className="container">
                                <div className="row">
                                    <div className="col-md-6">
                                        <div className="card">
                                            <div className="card-header bg-secondary text-white">
                                                <p className="h3">Event Information</p>
                                            </div>
                                            <div className="card-body">
                                                <form onSubmit={submitEvents}>
                                                    <div className="form-group">
                                                        <input
                                                            name="name"
                                                            value={events.name}
                                                            onChange={updateInput}
                                                            required
                                                            type="text" className="form-control" placeholder="Event Name"/>
                                                    </div>
                                                    <div className="form-group">
                                                        <input
                                                            name="image"
                                                            value={events.image}
                                                            onChange={updateInput}
                                                            required
                                                            type="text" className="form-control" placeholder="Upload Image as url"/>
                                                    </div>
                                                    <div className="form-group">
                                                        <input
                                                            name="date"
                                                            value={events.date}
                                                            onChange={updateInput}
                                                            required
                                                            type="date" className="form-control" placeholder="Date of Event "/>
                                                    </div>
                                                    <div className="form-group">
                                                        <select
                                                            name="type"
                                                            value={events.type}
                                                            onChange={updateInput}
                                                            required
                                                            className="form-control">
                                                            <option value="">Event Type</option>
                                                            <option value="FREE" name="free">Free</option>
                                                            <option value="PRO" name="pro">Pro</option>
                                                        </select>
                                                    </div>
                                                    <div className="form-group">
                                                        <input
                                                            name="price"
                                                            value={events.price}
                                                            onChange={updateInput}
                                                            required
                                                            type="number" className="form-control" placeholder="Price"/>
                                                    </div>
                                                    <div className="form-group">
                                            <textarea
                                                name="info"
                                                value={events.info}
                                                onChange={updateInput}
                                                required
                                                className="form-control" rows="3" placeholder="Information"/>
                                                    </div>
                                                    <div className="form-group" >
                                                        <input type="submit" className="btn btn-secondary btn-sm"/>
                                                    </div>
                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </section>
                    </React.Fragment> :

                    <React.Fragment>
                        <section>
                            <div className="container">
                                <div className="row text-center">
                                    <div className="col">
                                        <p className="h3 text-danger">You are not Authorized to upload an Event</p>
                                        <small>If you want to upload the same? you need to contact DBA administration to allow you as a Admin.</small>
                                    </div>
                                </div>
                            </div>
                        </section>
                    </React.Fragment>
            }
            <div style={{marginBottom : '50px'}}/>
        </React.Fragment>
    )
};
export default UploadEvents;