import * as userAction from './user.action';

//features key
export  const userFeaturesKey = 'users';

//initial state
let initialSatate = {
    loading:false,
    errorMessage :'',
    user:{},
    token:'',
    isAuthenticated : false
};

//reducer function
export const reducer =(state=initialSatate , action)=>{
    let {type , payload}=action;

    switch (type){
        case userAction.USER_REGISTER_REQUEST:
            return{...state , loading:true};
        case userAction.USER_REGISTER_SUCCESS:
            return{
                ...state,
                loading:false
            };
        case userAction.USER_REGISTER_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload
            };

            //login users
        case userAction.USER_LOGIN_REQUEST:
            return{...state , loading:true};
        case userAction.USER_LOGIN_SUCCESS:
            localStorage.setItem(process.env.REACT_APP_LOGIN_TOKEN , payload.token);
            return{
                ...state,
                loading:false,
                token:payload.token,
                user : payload.user,
                isAuthenticated: true
            };
        case userAction.USER_LOGIN_FAILURE:
            return{
                ...state,
                loading:false,
                errorMessage:payload,
                user:{},
                token:'',
                isAuthenticated : false
            };

            //get userInfo
        case userAction.GET_USER_INFO_REQUEST:
            return{
                ...state,
                loading : true
            };
        case userAction.GET_USER_INFO_SUCCESS:
            return{
                ...state,
                loading:true,
                user : payload.user
            };
        case userAction.GET_USER_INFO_FAILURE:
            return{
                ...state,
                loading:false,
                user : {}
            };
            //logoutUser
        case userAction.LOGOUT_USER:
            localStorage.removeItem(process.env.REACT_APP_LOGIN_TOKEN);
            return{
                user:{},
                token:'',
                isAuthenticated : false
            };

        default: return state;
    }
};
