import {combineReducers} from 'redux';
import * as alertReducer  from './alert/alert.reducer';
import * as userReducer  from './user/user.reducer';
import * as eventReducer from './events/events.reducer';
const rootReducer = combineReducers({

[alertReducer.alertFeatures] : alertReducer.reducer,
[userReducer.userFeaturesKey]:userReducer.reducer,
    [eventReducer.eventFeaturesKey]:eventReducer.reducer

    });
export {rootReducer};