import Axios from 'axios';
import {useHistory} from 'react-router-dom';
import * as alertActions from '../alert/alert.action';
import * as userUtil from '../../util/userUtil';
import * as tockenUtil from '../../util/tockenUtil';

//ACTIONS type
export  const USER_REGISTER_REQUEST = 'USER_REGISTER_REQUEST';
export  const USER_REGISTER_SUCCESS = 'USER_REGISTER_SUCCESS';
export  const USER_REGISTER_FAILURE = 'USER_REGISTER_FAILURE';


export  const USER_LOGIN_REQUEST = 'USER_LOGIN_REQUEST';
export  const USER_LOGIN_SUCCESS = 'USER_LOGIN_SUCCESS';
export  const USER_LOGIN_FAILURE = 'USER_LOGIN_FAILURE';

export const GET_USER_INFO_REQUEST='GET_USER_INFO_REQUEST';
export const GET_USER_INFO_SUCCESS='GET_USER_INFO_SUCCESS';
export const GET_USER_INFO_FAILURE='GET_USER_INFO_FAILURE';

export  const LOGOUT_USER = 'LOGOUT_USER';



//real action
export const registerUser = (user , history)=>{
    return async (dispatch)=>{
        try {
            dispatch({type : USER_REGISTER_REQUEST});
            let dataURL = 'http://127.0.0.1:5000/api/users/register';
            let response = await Axios.post(dataURL, user);
            dispatch({type : USER_REGISTER_SUCCESS , payload : response.data});
            dispatch(alertActions.setAlert(response.data.msg , 'success'));
            history.push('/users/login');
        }
        catch (error) {
            console.log(error);
            dispatch({type : USER_REGISTER_FAILURE , payload:error.response.data});
            let errorList = error.response.data.errors;
            for (let error of errorList){
                dispatch(alertActions.setAlert(error.msg , 'danger'));
            }
        }
    }
};





//user login
export const loginUser = (user , history)=>{
    return async (dispatch)=>{
        try {
            dispatch({type : USER_LOGIN_REQUEST});
            let dataURL = 'http://127.0.0.1:5000/api/users/login';
            let response = await Axios.post(dataURL, user);
            dispatch({type : USER_LOGIN_SUCCESS , payload : response.data});
            dispatch(alertActions.setAlert(response.data.msg , 'success'));
            history.push('/');
        }
        catch (error) {
            dispatch({type : USER_LOGIN_FAILURE , payload:error.response.data});
            let errorList = error.response.data.error;
            for (let error of errorList){
                dispatch(alertActions.setAlert(error.msg , 'danger'));
            }
        }
    }
};


//get userInfo

export  const getUserInfo=()=>{
    return async (dispatch)=>{
        if(userUtil.getToken()){
            tockenUtil.setAuthToken(userUtil.getToken());
        }
        try {
            dispatch({type :GET_USER_INFO_REQUEST});
            let dataUrl = 'http://127.0.0.1:5000/api/users/';
            let response = await Axios.get(dataUrl);
            dispatch({type : GET_USER_INFO_SUCCESS , payload : response.data});
        }
        catch (error) {
            console.log(error);
            dispatch({type : GET_USER_INFO_FAILURE , payload:error})
        }
    }
};


//logout user function

export  const logoutUser = (history)=>{
    return (dispatch)=>{
        dispatch({type : LOGOUT_USER});
        history.push('/');
    }
};