import React,{useEffect} from 'react';
import "./App.css"
import {BrowserRouter as Router , Route , Switch} from 'react-router-dom';
import Home from '../src/root/layout/home/Home'
import FreeEvents from "./events/free/FreeEvents";
import ProEvents from "./events/pro/ProEvents";
import Upload from "./events/pro/ProEvents";
import UploadEvents from "./events/upload/UploadEvents";
import Login from "./users/login/Login";
import Register from "./users/register/Register";
import Navbar from "./root/layout/navbar/Navbar";
import Alert from "./root/util/alert/Alert";
import * as userAction from './redux/user/user.action';
import * as userUtil from './util/userUtil';
import {useDispatch} from 'react-redux';
import PrivateRoot from "./util/privateRoot";

let App = ()=>{

    let dispatch = useDispatch();
    useEffect(()=>{
        if (userUtil.getToken()){
            dispatch(userAction.getUserInfo());
        }
    });

    return (
        <React.Fragment>
            <Router>
                <Navbar/>
                <Alert/>
                <Switch>
                    <Route exact  path='/' component={Home}/>
                    <Route exact path="/events/free" component={FreeEvents}/>
                    <PrivateRoot exact path="/events/pro" component={ProEvents}/>
                    <PrivateRoot exact path="/events/upload" component={UploadEvents}/>
                    <Route exact path="/users/login" component={Login}/>
                    <Route exact path="/users/register" component={Register}/>
                </Switch>
            </Router>
        </React.Fragment>
    );
};


export default App;
