import React,{useState} from 'react';
import {Link , useHistory} from 'react-router-dom';
import {useDispatch} from 'react-redux';
import * as alertActions from '../../redux/alert/alert.action';
import * as userAction from '../../redux/user/user.action';
let Register = ()=>{

    let dispatch = useDispatch();
    let history = useHistory();
    let [user , setUser] = useState({
        name : '',
        email : '',
        password : ''

    });

    let [userError , setUserError] = useState({
        nameError : '',
        emailError : '',
        passwordError : ''

    });

    let validateUserName = (event)=>{
        setUser({
            ...user,
            name : event.target.value
        });
        let regExp = /^[a-zA-Z0-9 ]{4,15}$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError ,nameError:'Enter a proper name' }) :

            setUserError({...userError , nameError : ''});
    };
    let validateUserEmail = (event)=>{
        setUser({...user , email:event.target.value});
        let regExp = /^\w+([\\.-]?\w+)*@\w+([\\.-]?\w+)*(\.\w{2,3})+$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError , emailError:'Enter correct Email' }):setUserError({...userError , emailError : ''})
    };

    let validateUserPassword = (event)=>{
        setUser({...user , password : event.target.value});
        let regExp = /^[A-Za-z]\w{7,14}$/;
        !regExp.test(event.target.value) ?
            setUserError({...userError , passwordError : 'Enter correct password'}) : setUserError({...userError , passwordError :''});
    };


    //submitRegistration
    let submitRegistration =(event)=>{
        event.preventDefault();
        if(user.name !=='' && user.email !== '' && user.password !==''){
            dispatch(userAction.registerUser(user , history))
        }
        else {
            dispatch(alertActions.setAlert('Please fill the fields' , 'danger'));
        }
    };




    


    return(
        <React.Fragment>
            {/*<pre>{JSON.stringify(user)}</pre>
            <pre>{JSON.stringify(userError)}</pre>*/}
            <section className="mt-5">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-md-4">
                            <div className="card animated zoomInUp">
                                <div className="card-header bg-secondary text-white ">
                                    <p className="h3">Register Here</p>
                                </div>
                                <div className="card-body">
                                    <form onSubmit={submitRegistration}>
                                        <div className="form-group">
                                            <input
                                                name="name"
                                                value={user.name}
                                                onChange={validateUserName}
                                                type="text" className={`form-control ${userError.nameError.length > 0 ? 'is-invalid' : ''}` } placeholder="Enter Name"/>
                                            {userError.nameError.length>0 ? <small className="text-danger">{userError.nameError}</small> : ''}
                                        </div>
                                        <div className="form-group">
                                            <input
                                                name="email"
                                                value={user.email}
                                                onChange={validateUserEmail}
                                                type="email" className={`form-control ${userError.emailError.length > 0 ? 'is-invalid' : ''}`} placeholder="Enter email"/>
                                            {userError.emailError.length > 0 ? <small className="text-danger">{userError.emailError}</small> : ''}
                                        </div>
                                        <div className="form-group">
                                            <input
                                                name="password"
                                                value={user.password}
                                                onChange={validateUserPassword}
                                                type="password" className={`form-control ${userError.passwordError.length > 0 ? 'is-invalid' : ''}`} placeholder="Enter Password"/>
                                            {userError.passwordError.length > 0 ? <small className="text-danger">{userError.passwordError}</small> : ''}
                                        </div>
                                        <input type="submit" className="btn btn-secondary btn-sm" value="Register"/>
                                    </form>
                                </div>
                                <div className="card-footer">
                                    <small>If already have a account ? <Link to='/users/login'>Login</Link> </small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="mt-5">
                        <small>Password guide:-</small><hr/>
                        <small>Should't contain special characters.</small><br/>
                        <small>Minimum 7 characters.</small><br/>
                        <small>Maximum  15 characters.</small><br/>
                        <small>It may contains Capital letters(Uppers case),Small letters(Lower case) and digits.</small><br/>
                    </div>
                </div>
            </section>
        </React.Fragment>
    )
};
export default Register;